import React from "react"

const NavLink = props => (
  <li style={{
    display: `inline-block`
    }}
    >
    <a href={props.to}>{props.text}</a>
  </li>
)

export default () => (
  <div style={{
      display: `inline-block`,
      verticalAlign: `top`,
      backgroundColor: `yellow`,
      float: `right`
    }}
    >
    <ul>
      <NavLink to="/" text="Home" />
      <NavLink to="/docs" text="Docs" />
      <NavLink to="/about/" text="About" />
      <NavLink to="/contact/" text="Contact" />
      <NavLink to="https://github.com/alekslitvinenk/docker-openvpn" text="GitHub" />
    </ul>
  </div>
)